body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 0;
}

.App {
  font-size: 0.75em;
  text-align: center;
  background-color: #222;
  background-image: url("data:image/svg+xml,%3Csvg width='52' height='26' viewBox='0 0 52 26' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23444444' fill-opacity='0.4'%3E%3Cpath d='M10 10c0-2.21-1.79-4-4-4-3.314 0-6-2.686-6-6h2c0 2.21 1.79 4 4 4 3.314 0 6 2.686 6 6 0 2.21 1.79 4 4 4 3.314 0 6 2.686 6 6 0 2.21 1.79 4 4 4v2c-3.314 0-6-2.686-6-6 0-2.21-1.79-4-4-4-3.314 0-6-2.686-6-6zm25.464-1.95l8.486 8.486-1.414 1.414-8.486-8.486 1.414-1.414z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.Page {
  min-height: 100vh;
  color: #aaa;
  padding: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
}

.PageTitle {
  font-size: 5em;
}

.PageContent {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  max-width: 800px;
  font-size: 2em;
  text-align: justify;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.TextButton {
  margin: 4px 8px;
  color: #aaa;
  font-size: 1em;
  cursor: pointer;
}

.UnderLine {
  background-color: #aaa;
  height: 2px;
  width: 0%;
  margin: 0px auto;
  transition: width 0.2s ease;
}

.TextButton:hover .UnderLine {
  width: 100%;
}

.ArrowButtonWrapper {
  padding: 8px;
}

.ArrowButton {
  height: 20px;
  width: 20px;
  border: solid #aaa;
  border-width: 0 4px 4px 0;
  display: inline-block;
  padding: 4px;
  margin: 8px;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.Down {
  -webkit-transform: translateY(-25%) rotate(45deg);
          transform: translateY(-25%) rotate(45deg);
}

.ArrowButtonWrapper:hover .Down {
  -webkit-transform: translateY(-25%) rotate(45deg) scale(1.5);
          transform: translateY(-25%) rotate(45deg) scale(1.5);
}

.Up {
  -webkit-transform: translateY(25%) rotate(-135deg);
          transform: translateY(25%) rotate(-135deg);
}

.ArrowButtonWrapper:hover .Up {
  -webkit-transform: translateY(25%) rotate(-135deg) scale(1.5);
          transform: translateY(25%) rotate(-135deg) scale(1.5);
}

.HelloPage {
  background-color: transparent;
  border-color: #bbb;
  color: #bbb;
}

.HelloPage .PageContent {
  -webkit-justify-content: center;
          justify-content: center;
}

.Title {
  text-align: center;
}

.subtitle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0px;
}

.HelloPage .ArrowButton {
  border-color: #aaa;
}

.AboutPage {
  background-color: #333;
}

.SkillsPage {
  background-color: #222;
}

.SkillList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.SkillDescription {
  margin: 48px 0px;
}

.SkillEntry {
  box-sizing: border-box;
  display: inline-block;
  border: 4px solid transparent;
  padding: 8px;
  margin: 8px;
  transition: border 0.2s ease;
}

.SkillEntry:hover {
  border: 4px solid #aaa;
}

.active {
  border: 4px solid #aaa;
}

.SkillEntry i {
  font-size: 7.5rem;
}

.SkillRank {
  padding: 8px 0px 0px 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.SkillRankMarkActive {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 8px solid #aaa;
  border-radius: 16px;
  box-sizing: border-box;
}

.SkillRankMark {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 4px solid #aaa;
  border-radius: 16px;
  box-sizing: border-box;
}

.ProjectsPage {
  background-color: #111;
}

.ProjectsPage .PageContent {
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.ProjectsList {
  padding: 16px;
}

.ProjectEntry {
  margin: 8px;
  padding: 8px;
  border: 4px solid transparent;
  transition: border-color 0.2s ease;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.ProjectEntry i {
  font-size: 4rem;
  margin: 0px 8px 0px 0px;
}

.ProjectEntry:hover {
  border-color: #aaa;
}

.ProjectLink {
  color: inherit;
  text-decoration: none;
}

.ProjectEntryContent {
  display: inline-block;
}

.ProjectEntryTitle {
  font-size: 1em;
}

.ProjectEntrySubtitle {
  font-size: 0.75em;
}

.GithubButton {
  display: inline-block;
  text-align: center;
  margin: 8px;
  padding: 8px;
  border: 4px solid transparent;
  transition: border-color 0.2s ease;
}

.GithubButton:hover {
  border-color: #aaa;
}

.GithubButton i {
  font-size: 3em;
}

.ContactPage {
  background-color: #000;
  min-height: 33vh;
}

.EmailWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0 0 48px 0;
}

.ContactPage i {
  margin: 0 16px 0 0;
  font-size: 2em;
}

.NavBar {
  position: fixed;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  height: 48px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-size: 2em;
  padding-right: 16px;
  z-index: 1;
  background-color: rgba(34, 34, 34, 0.5);
}

